.about{
  background-color: hsl(210, 14%, 97%);
  &__container{
    padding: 50px 20px;
  }
  &__title{
    margin: 0;
    color: #232123;
    font-family: Playfair Display;
    font-size: 49px;
    font-weight: 700;
    line-height: 49px;
    text-align: left;
    position: relative;
    margin-bottom: 40px;

    &::after{
      content: '';
      position: absolute;
      top: 60px;
      left: 0px;
      width: 174px;
      height: 2px;
      background-color: #A80D0F;
    }
  }
  &__subtitle{
    margin: 0;
    font-size: 16px;
    font-weight: 400;
    line-height: 22.4px;
    text-align: left;
    color: #A80D0F;
    margin-bottom: 10px;
  }
  &__inner  {
    display: flex;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto;
  }
  &__left{
    max-width: 564px;
  }
  &__right{
    position: relative;
    width: 482px;
    height: 552px;
    border-radius: 20px;
    background-image: url('../img/about-1.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
  &__flag{
    position: absolute;
    left: -100px;
    bottom: -20px;
  }
  &__descr{
    margin: 0;
    color: #232123;
    font-size: 16px;
    font-weight: 400;
    line-height: 22.4px;
    text-align: left;
    margin-bottom: 10px;
  }
  &__text{
    margin: 0;
    padding-top: 10px;
    font-family: Playfair Display;
    font-size: 20px;
    font-weight: 500;
    line-height: 24px;
    text-align: left;
    color: #232123;
    margin-bottom: 20px;
  }
  &__list{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
  &__item{
    display: flex;
    justify-content: start;
    align-items: flex-end;
    background-image: url('../img/check.svg');
    background-position: top left;
    background-repeat: no-repeat;
    min-width: 175px;
    height: 101px;
  }
  &__text-item{
    margin: 0;
    font-family: Playfair Display;
    font-size: 18px;
    font-weight: 400;
    line-height: 25.2px;
    text-align: left;
    color: #232123;
  }
  &__hidden{
    display: none;
    margin: 0 auto;
    max-width: 150px;
    color: #232123;
    font-family: Playfair Display;
    font-size: 49px;
    font-weight: 700;
    line-height: 49px;
    text-align: center;
    position: relative;
    margin-bottom: 40px;

    &::after{
      content: '';
      position: absolute;
      top: 40px;
      left: 45px;
      width: 60px;
      height: 2px;
      background-color: #A80D0F;
    }
    padding-bottom: 40px;
  }

}

@media (max-width: (992px)){
  .about__hidden{
    display: block;
    padding-bottom: 0px;
    font-family: Playfair Display;
    font-size: 32px;
    font-weight: 700;
    line-height: 32px;
  }
  .about__title{
    display: none;
  }
  .about__inner{
    flex-direction: column-reverse;
    align-items: center;
    gap: 40px;
  }
  .about__right{
    position: relative;
    width: 440px;
  }
  .about__flag{
    width: 121.26px;
    height: 86.53px;
    left: -20px;
    bottom: -20px;
  }
}

@media (max-width: (576px)){
  .about__right{
    width: 390px;
    height: 366px;
    border-radius: 20px;
  }
}

@media (max-width: (400px)){
    .about__right{
      width: 320px;
      height: 366px;
      border-radius: 20px;
  }
}

